.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 80%;
  max-height: calc(100vh - 120px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.modal-enter {
  opacity: 0;
}

.modal-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}

.modal-exit {
  opacity: 1;
}

.modal-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}
/*
Amplify login styling
*/
.amplify-authenticator {
  --amplify-primary-color: #152939;
  --amplify-primary-tint: #37475a;
  --amplify-primary-shade: #0e1c28;
  background-color: #1a202c;
  color: #ffffff;
}

.amplify-button {
  background-color: var(--amplify-primary-color);
  color: #ffffff;
}

.amplify-button:hover {
  background-color: var(--amplify-primary-tint);
}

.amplify-button:active {
  background-color: var(--amplify-primary-shade);
}

.amplify-form-section {
  border-radius: 8px;
}

.amplify-input,
.amplify-form-row {
  background-color: #2d3748;
  color: #ffffff;
}

.amplify-input,
.amplify-label {
  color: #ffffff;
}
.pulsing {
  animation: pulse 1s infinite alternate;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}